export function beSureBtnClick(props) {
    let dbsource = this.state.dbsource;
    if (Object.keys(dbsource).length == 0) {
        toast({ color: STATUS.warning, content: '[],' });/*  */
        return;
    }
    let begdate = this.state.vdate;
    if (!begdate) {
        toast({ color: STATUS.warning, content: '[],' });/*  */
        return;
    }
    let transtypepk = this.state.transtypepk;
    if (Object.keys(transtypepk).length == 0) {
        toast({ color: STATUS.warning, content: '[],' });/*  */
        return;
    }
    let param = {
        ref1: dbsource,
        daterange: begdate,
        transtype: transtypepk.map(item => { return item.refcode }),
    };
    ajax({
        url: REQUEST_URL.down,
        data: param,
        success: (res) => {
            if (res.success) { //
                if (res.data) {
                    promptBox({
                        color: STATUS.success,
                        title: "",
                        content: res.data,
                        beSureBtnClick: () => {
                            if (res.data.indexOf("") != -1) {
                                //
                                let stardate = begdate[0].split(" ")[0];
                                let enddate = begdate[1].split(" ")[0];
                                props.search.setSearchValByField(LIST.search_id, 'dbusdate', { display: stardate+'~'+enddate, value: begdate[0]+','+begdate[1]});
                                  
                                listRefresh({ ...props, json: this.state.json });
                            }
                        }
                    });
                }
            } else { //
                toast({ color: STATUS.warning, content: '!' });
            }
        }
    });


}

