export function listSearch(props, queryInfo) {
    let pageInfo = props.table.getTablePageInfo(LIST.table_id);
    if (!queryInfo) {
        queryInfo = props.search.getQueryInfo(LIST.search_id);
    }
    queryInfo.pageInfo = pageInfo;
    queryInfo.pageCode = LIST.page_code;

    // 
    props.button.setDisabled({ [LIST_BUTTON.refresh]: false });
    //
    props.button.setButtonDisabled(LIST_DISABLED_BUTTON, true);
    ajax({
        url: REQUEST_URL.queryList,
        data: queryInfo,
        success: (res) => {
            let { success, data } = res;
            if (res.formulamsg && res.formulamsg instanceof Array && res.formulamsg.length > 0) {
                props.dealFormulamsg(res.formulamsg);
            }
            if (success && data && data[LIST.table_id]) {
                props.table.setAllTableData(LIST.table_id, data[LIST.table_id]);
                toast({ color: STATUS.success });
            } else {
                props.table.setAllTableData(LIST.table_id, { rows: [] });
                toast({ color: STATUS.warning, content: props.json['M0Z20602-000010'] });/*  */
            }

            // 
            let { setDefData } = cardCache;
            setDefData(SEARCH_CACHE.key, SEARCH_CACHE.dataSource, queryInfo);
        }
    });
}

export function listLinkQuery(props, data) {
    let sels = props.table.getCheckedRows(LIST.table_id);
    if (sels.length == 0) {
        toast({ color: STATUS.warning, content: "" });/* */
        return;
    }
    if (sels.length > 1) {
        toast({ color: STATUS.warning, content: "" });/* */
        return;
    }

    let param = {
        pk_group: sels[0].data.values.pk_group.value,
        pk_org: sels[0].data.values.pk_group.value,
        cbillid: sels[0].data.values.pk_clinic_income.value,
        vtrantypecode: sels[0].data.values.transtype.value,
    };

    ajax({
        url: REQUEST_URL.linkquery,
        data: param,
        success: (res) => {
            let { success, data } = res;
            if (success && data) {
                let srcCode = res.data.src;
                if ('_LinkVouchar2019' == srcCode) {
                    //
                    if (res.data.des) {
                        //
                        if (res.data.pklist) {
                            if (res.data.pklist.length == 1) {
                                //
                                props.openTo(res.data.url, {
                                    status: 'browse',
                                    appcode: res.data.appcode,
                                    pagecode: res.data.pagecode,
                                    id: res.data.pklist[0],
                                    n: '',
                                    backflag: 'noback'
                                });
                                return;
                            }
                        }
                    }
                } else {
                    promptBox({
                        color: "info", // "success" "success"/"info"/"warning"/"danger",
                        title: "", // , ""/""/""/"",
                        content: "!", // ,
                        noCancelBtn: true, // ,(false),
                    });
                }
            }
        }
    });
}

