/**
 * 
 * @param {*} res           response
 * @param {*} opername      
 */
export function PromptMessage(res) {
    let { status, msg } = res.data;
    let content;
    let total = res.data.total;
    let successNum = res.data.successNum;
    let failNum = res.data.failNum;
    content = '' + '' + total + '';/*  ,*/
    content = content + ',' + successNum + '';/*  , ,,*/
    content = content + ',' + failNum + '';/*  ,,*/
    let errMsgArr = res.data.errormessages;
    //
    let msgArr = "";
    if (res.data.data && res.data.data.length > 0) {
        let data = res.data.data;
        for (var i = 0; i < data.length; i++) {
            msgArr += (" \n =" + data[i].vbillno + ">>" + data[i].msg);
        }
    }
    //
    if (status == 0) {
        toast({
            color: "success",
            title: '' + msg,
            content: content + msgArr,
            TextArr: ['', '', ''],/*  ,,*/
            groupOperation: true
        });
    }
    //
    else if (status == 1) {
        toast({
            duration: "infinity",
            color: "danger",
            title: '' + msg,
            content: content + msgArr,
            TextArr: ['', '', ''],/*  ,,*/
            groupOperation: true,
            groupOperationMsg: errMsgArr
        });
    }
    //
    else if (status == 2) {
        toast({
            duration: "infinity",
            color: "warning",
            title: '' + msg,
            content: content + msgArr,
            TextArr: ['', '', ''],/*  ,,*/
            groupOperation: true,
            groupOperationMsg: errMsgArr
        });
    }
}

