export function onPrint(props) {
    let selectDatas = props.table.getCheckedRows(LIST.table_id);
    if (selectDatas.length === 0) {
        toast({ color: STATUS.warning, content: "" });/* :*/
        return;
    }
    let pks = [];
    selectDatas.forEach((item, key) => {
        pks.push(item.data.values[PRIMARY_KEY.head_id].value);
    });
    print(
        'pdf',
        REQUEST_URL.Print,
        {
            funcode: props.appcode,//
            nodekey: 'M0Z20602_CARD',//
            oids: pks,
            //outputType:'output'
        }
    )
}

