export function onSearchAfterEvent(props) {
    this.setState({ dbsource: {}, pk_org: "", vdate: ['', ''], transtypepk: '' });
    props.button.setButtonDisabled(LIST_BUTTON.down, false);

    let pk_org = props.search.getSearchValByField(LIST.search_id, 'pk_org');
    if (pk_org && pk_org.value && pk_org.value.firstvalue) {
        let value = pk_org.value.firstvalue;
        this.setState({ pk_org: value });
    } else {
        props.button.setButtonDisabled(LIST_BUTTON.down, true);
    }

}

//
export function commitRefresh(props) {
    let { getDefData } = cardCache;
    let queryInfo = getDefData(SEARCH_CACHE.key, SEARCH_CACHE.dataSource);
    let pageInfo = props.table.getTablePageInfo(LIST.table_id);
    if (!queryInfo) {
        queryInfo = props.search.getQueryInfo(LIST.search_id);
    }
    queryInfo.pageInfo = pageInfo;
    queryInfo.pageCode = LIST.page_code;

    // 
    props.button.setDisabled({ [LIST_BUTTON.refresh]: false });
    //
    props.button.setButtonDisabled(LIST_DISABLED_BUTTON, true);
    ajax({
        url: REQUEST_URL.queryList,
        data: queryInfo,
        success: (res) => {
            let { success, data } = res;
            if (res.formulamsg && res.formulamsg instanceof Array && res.formulamsg.length > 0) {
                props.dealFormulamsg(res.formulamsg);
            }
            if (success && data && data[LIST.table_id]) {
                props.table.setAllTableData(LIST.table_id, data[LIST.table_id]);
                //toast({ color: STATUS.success });
            } else {
                props.table.setAllTableData(LIST.table_id, { rows: [] });
                toast({ color: STATUS.warning, content: props.json['M0Z20602-000010'] });/*  */
            }

            // 
            let { setDefData } = cardCache;
            setDefData(SEARCH_CACHE.key, SEARCH_CACHE.dataSource, queryInfo);
        }
    });
}

//
export function checkOrg(props,data) {
    let pk_org = data.context.pk_org;
    let org_Name = data.context.org_Name;
    ajax({
        url: REQUEST_URL.checkOrg,
        data: pk_org,
        success: (res) => {
            if (res.success) { //
              if(res.data && res.data =="Y"){
                props.search.setSearchValByField(LIST.search_id, FIELD.org, { display: org_Name, value: pk_org });
                this.setState({ pk_org: pk_org });
              }else{
                props.button.setButtonDisabled(LIST_BUTTON.down, true);
              }
            } 
        }
    });
}

